html {
  font-size: 16px;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  color: #595959;
  font-family: "Lato", sans-serif;
  font-style: normal;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a,
a:visited,
a:link {
  color: #79CD47;
  text-decoration: none;
  outline: none;
  transition: all 0.5s ease;
}

a:hover {
  color: #65BA32;
  text-decoration: none;
  outline: none;
}

ul,
ol {
  clear: both;
}

.fl {
  float: left;
}

.fr {
  float: right;
}

.fn {
  float: none;
  display: inline-block;
}

.tal {
  text-align: left;
}

.tar {
  text-align: right;
}

.tac {
  text-align: center;
}

.clr-l {
  clear: left;
}

.clr-r {
  clear: right;
}

.clr,
.clear {
  clear: both;
}

.nodisp {
  display: none;
}

h1 {
  font-style: normal;
  font-size: 2.5rem;
}
@media only screen and (min-width: 768px) {
  h1 {
    font-size: 4rem;
  }
}

h2 {
  color: #555;
  font-size: 1.875rem;
}
@media only screen and (min-width: 768px) {
  h2 {
    font-size: 3.5rem;
  }
}

h3 {
  color: #555;
  font-size: 1.25rem;
}
@media only screen and (min-width: 768px) {
  h3 {
    font-size: 2rem;
  }
}

p {
  padding-bottom: 2rem;
}

p, li {
  line-height: 2rem;
}

ol, ul {
  padding-bottom: 2rem;
}

.img-resp {
  max-width: 100%;
  height: auto;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  100% {
    opacity: 1;
    visibility: visible;
  }
}
@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeInDown {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
